.isDark {
    background-color: black;
}
.titlebar {
    text-align: center;
}
.titlebar > h1 {
    font-family: 'Luckiest Guy', cursive;
    font-size: 10rem;
    color: yellow;
    letter-spacing: .01rem;
    text-shadow: 6px 6px 0px rgba(0,0,0,0.2);
}

.titlebar > h1:hover {
    font-size: 13rem;
}
#titlebar-jumbotron {
    max-height: 300px;
    background-image: url(/turbo-potato-react-game/static/media/eggBkg.db88f0e1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; 
}


@media only screen and (min-width : 681px) and (max-width: 960px) {
    .titlebar > h1 {
        font-size: 6em;
    }
    .titlebar > h1:hover {
        font-size: 6.5em;
    }
    
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    .titlebar > h1 {
        font-size: 4em;
    }
    .titlebar > h1:hover {
        font-size: 4.5em;
    }
    
}
.scoreBox > h1 {
    font-size: 25pt;
    font-family: 'Luckiest Guy', cursive;
}


@media only screen and (min-width : 681px) and (max-width: 960px) {
    
    .scoreBox > h1 {
        font-size: 20pt;
    }
    
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    .scoreBox > h1 {
        font-size: 14pt;
    }
    
}
.goalArea > h1 {
    font-size: 25pt;
    font-family: 'Luckiest Guy', cursive;
}

@media only screen and (min-width : 681px) and (max-width: 960px) {
    
    .goalArea > h1 {
        font-size: 20pt;
    }
    
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    .goalArea > h1 {
        font-size: 14pt;
    }
    
}
#playboxImg {
    height: 10em;
    width: 8em;
}

.playbox:hover {
    -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    #playboxImg {
        height: 150px;
        width: 180px;
    }
    
}
.playArea {
    display: flex;
    justify-content: space-around;

}


/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    .playArea {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
    
    }
}
#bunnyPic {
    width: 200px;
    height: 160px;
}


/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-width : 320px) and (max-width : 680px) {
    /* Styles */
    #bunnyPic {
        width: 60px;
        height: 80px;
    }
}
